/*eslint no-unused-vars: "off"*/
/*eslint-disable @typescript-eslint/no-unused-vars */

import React, { useState } from "react"
import { Button, Card, CardBody, CardHeader, CardText } from "reactstrap"
import { HServiceWorker } from "src/core/helpers"

const linkSW = "/service-worker-notify.js"

const PageDev = () => {
  return (
    <div>
      <Card>
        <CardHeader className="bg-light" onClick={() => {}}>
          <h5 className="mb-0">Notification</h5>
        </CardHeader>
        <CardBody>
          <div className="mb-3">
            <Button
              color="primary"
              onClick={() => {
                HServiceWorker.enableNotification(linkSW)
              }}
            >
              Turn On
            </Button>
          </div>
          <div>
            <Button
              color="info"
              onClick={() => {
                HServiceWorker.showNotification(linkSW, null, null, null)
              }}
            >
              Test
            </Button>
          </div>
        </CardBody>
      </Card>
    </div>
  )
}

export default PageDev
